* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.state {
  max-width: 640px;
  width: calc(100% - 20px);
  height: 100vh;
  margin: 0 auto;
  border: 2px solid rgb(0, 0, 0);
  padding: 30px;
  text-align: center;
  background-color: rgb(0, 0, 0);
}
.state h1 {text-align: center;
color:  white;
margin-bottom: 20px;
font-size: 40px;}
.state img {
  width: 80%;
  height: 60%;
  object-fit: cover;
  margin: 0 auto;
  display: block;
  border-radius: 30px 30px 0 0;
}
.state input {
  width: 80%;
  height: 45px;
  display: block;
  margin: 0 auto;
  border: 0;
  border-radius: 0 0 30px 30px;
}
.state input::placeholder {
  text-align: center;
  color: black;
  
}
.state button {
  background-color: rgb(255, 255, 255);
  border: 0;
  padding: 10px 30px;
  color: rgb(0, 0, 0);
  border-radius: 40px;
  font-size: 20px;
  cursor: pointer;
  margin-top: 30px;
  font-weight: bold;
  transition: 0.25s;
}
.state button:hover {
  background-color: black;
  color: white;
}
.state p {
  color: white;
  margin-top: 10px;
}
.state p span {

}
